import Vue from "vue";
import Router from "vue-router";
import Login from "./components/user/Login.vue";
import Regist from "./components/user/Regist.vue";
import Home from "./components/Home.vue";
import Index from "./components/index/Index.vue";
import store from "./store/store";
import Book from "./components/books/Book.vue";

Vue.use(Router);

const routes = [
  {
    path: "/index",
    component: Index,
    // redirect: "/index/profile",
    // Ë∑ØÁî±ÂµåÂ•ó
    children: [],
    meta: { requireAuth: true, roles: ["admin", "user"] },
  },
  {
    path: "/",
    redirect: "/home",
  },
  {
    path: "/login",
    component: Login,
    meta: { requireAuth: false, roles: ["admin", "user"] },
  },
  {
    path: "/regist",
    component: Regist,
    meta: { requireAuth: false, roles: ["admin", "user"] },
  },
  {
    path: "/home",
    component: Home,
    meta: { requireAuth: false, roles: ["admin", "user"] },
  },
  {
    path: "/book/:book_id",
    component: Book,
    meta: { requireAuth: false, roles: ["admin", "user"] },
  },
];

const router = new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

let oRoutes = router.options.routes;

// ÁîüÊàêÂä®ÊÄÅË∑ØÁî±
const buildRouter = () => {
  let data = store.state.menu_data;
  data.forEach((item) => {
    item.child.forEach((child) => {
      let new_router = {
        path: child.path,
        component: () => import("./components" + child.componentPath),
      };
      oRoutes[0].children.push(new_router);
    });
  });
  router.addRoutes(oRoutes);
};

router.beforeEach((to, from, next) => {
  // Âà§Êñ≠Ë∑ØÁî±ÊòØÂê¶Â∑≤ÁªèÂä†ËΩΩËøá
  let isLoadRoute = store.state.isLoadRoute;
  if (!isLoadRoute) {
    // axios.get("/menuList").then((res) => {
    //   store.commit("setMeunData", res.data, menu_data);
    // });
    const menu_data = [
      {
        name: "USER",
        icon: "el-icon-user",
        child: [
          {
            name: "‰∏™‰∫∫‰ø°ÊÅØ",
            icon: "el-icon-document",
            path: "/index/profile",
            componentPath: "/index/user/Profile.vue",
          },
          {
            name: "ÊàëÁöÑÊî∂Ëóè",
            icon: "el-icon-collection",
            path: "/index/collection",
            componentPath: "/index/user/Collection.vue",
          },
          {
            name: "ÊàëÁöÑÂàÜ‰∫´",
            icon: "el-icon-share",
            path: "/index/share",
            componentPath: "/index/user/Share.vue",
          },
        ],
      },
      {
        name: "MORE",
        icon: "el-icon-more",
        child: [
          {
            name: "ËÅîÁ≥ªÊàë‰ª¨",
            icon: "el-icon-chat-dot-square",
            path: "/index/contact",
            componentPath: "/index/more/Contact.vue",
          },
          {
            name: "Â∞èÁ®ãÂ∫èÊúçÂä°",
            icon: "el-icon-mobile",
            path: "/index/more",
            componentPath: "/index/more/Contact.vue",
          },
        ],
      },
    ];
    // Ëé∑ÂèñÁî®Êà∑ËßíËâ≤
    let user_level = store.getters.getUserLevel;
    // Â¶ÇÊûúÁî®Êà∑‰∏∫ÁÆ°ÁêÜÂëòÂàôÊ∑ªÂä†Êï∞ÊçÆÂêéÂè∞
    if (user_level == 1) {
      menu_data.splice(1, 0, {
        name: "DATA&TABLE",
        icon: "el-icon-s-data",
        child: [
          {
            name: "Áî®Êà∑ÁÆ°ÁêÜ",
            icon: "el-icon-user-solid",
            path: "/index/userManager",
            componentPath: "/index/manager/UserManager.vue",
          },
          {
            name: "‰π¶Êú¨ÁÆ°ÁêÜ",
            icon: "el-icon-reading",
            path: "/index/bookManager",
            componentPath: "/index/manager/BookManager.vue",
          },
        ],
      });
    }
    store.commit("setMeunData", menu_data);
    // ÁîüÊàêÂä®ÊÄÅË∑ØÁî±
    buildRouter();
    // ËÆæÁΩÆÂ∑≤ÁªèÂä†ËΩΩËøáÁöÑÊ†áËÆ∞
    store.commit("setLoadRoute", true);
  }
  if (to.matched.some((m) => m.meta.requireAuth)) {
    // ÈúÄË¶ÅÁôªÂΩï
    if (window.localStorage.token && window.localStorage.isLogin === "1") {
      next();
    } else if (to.path !== "/login") {
      let token = window.localStorage.token;
      if (token === "null" || token === "" || token === undefined) {
        next({ path: "/login" });
        Vue.prototype.$message({
          message: "Ê£ÄÊµãÂà∞ÊÇ®ËøòÊú™ÁôªÂΩï,ËØ∑ÁôªÂΩïÂêéÊìç‰ΩúÔºÅ",
          type: "error",
          duration: 1500,
        });
      } else {
        next();
      }
    } else {
      next();
    }
  } else {
    // ‰∏çÈúÄË¶ÅÁôªÈôÜ
    next();
  }
});

export default router;
