          />
          <div class="book_info_text">
            ‰π¶Á±çÂêçÁß∞Ôºö<span style="color: #333">„Ää{{ book.book_name }}„Äã</span>
            <br />
            ÊâÄÂ±ûÂàÜÁ±ªÔºö <span style="color: #333">{{ book.class_name }}</span
            ><br />
            ‰π¶Á±ç‰ΩúËÄÖÔºö<span style="color: #333">{{ book.book_author }}</span
            ><br />
            ‰π¶Á±çÂá∫ÁâàÁ§æÔºö <span style="color: #333">{{ book.book_addr }}</span
            ><br />
            <el-rate
              v-model="book_rate"
              disabled
              show-score
              text-color="#ff9900"
              score-template="{value}"
            >
            </el-rate>
          </div>
        </div>
        <!-- Áî®Êà∑Êìç‰ΩúÊåâÈíÆ -->
        <div style="margin-top: 30px">
          <el-button
            style="width: 150px"
            icon="el-icon-download"
            @click="downloadBook"
            >‰∏ãËΩΩ</el-button
          >
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          <el-button type="warning" icon="el-icon-star-off" @click="collectBook"
            >Êî∂Ëóè</el-button
          >
        </div>
      </el-card>
      <!-- ÂÜÖÂÆπÁÆÄ‰ªã -->
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span class="header_name">ÂÜÖÂÆπÁÆÄ‰ªã</span>
        </div>
        <div class="text item">
          <p>
            {{ book.book_info }}
          </p>
        </div>
      </el-card>
      <!-- Áõ∏ÂÖ≥Êé®Ëçê(ÂêåÁ±ªÂûã+Âêå‰ΩúËÄÖ) -->
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span class="header_name">Áõ∏ÂÖ≥Êé®Ëçê</span>
        </div>
        <div style="text-align: center">
          <div class="book_item" v-for="rb in recommendBooks">
            <a :href="`/book/` + rb.book_id">
              <img
                :src="
                  `http://122.9.215.185:8081/static/img/books/` +
                  rb.book_img_url.split('/').at(-1)
                "
                alt="‰π¶Êú¨ÂõæÁâá"
              />
            </a>
          </div>
        </div>
      </el-card>
      <!-- ËØÑËÆ∫ÂàóË°® -->
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span class="header_name">ËØÑËÆ∫ÂàóË°®</span>
        </div>
        <div class="comment-item" v-if="this.comments.length === 0">
          üòä ÊöÇÊó†ËØÑËÆ∫ÔºåÂø´Êù•Êä¢Ê≤ôÂèëÂêß~
        </div>
        <div class="comment-item" v-for="comment in this.comments">
          <div style="padding-bottom: 10px; display: flex; align-items: center">
            <img
              style="border-radius: 50%; margin-right: 10px"
              :src="`http://122.9.215.185:8081` + comment.user_img_url"
              height="22px"
              alt=""
            />
            <span style="color: #4e86bd">{{ comment.user_name }}</span>
            <span style="color: #777; margin-left: 20px; margin-right: 20px">
              {{ comment.create_time }}
            </span>
            <el-popconfirm
              title="ËøôÊòØ‰∏ÄÊÆµÂÜÖÂÆπÁ°ÆÂÆöÂà†Èô§ÂêóÔºü"
              @confirm="delComment(comment.comment_id)"
            >
              <el-button
                slot="reference"
                type="text"
                style="color: #777"
                v-if="
                  $store.state.isLogin &&
                  comment.user_id === $store.getters.getUserId
                "
                icon="el-icon-delete"
              ></el-button>
            </el-popconfirm>
          </div>
          <div>{{ comment.comment_text }}</div>
        </div>
      </el-card>
      <!-- ÂèëË°®ËØÑËÆ∫ -->
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span class="header_name">ÂèëË°®ËØÑËÆ∫</span>
        </div>
        <div class="text item">
          <el-input
            type="textarea"
            :autosize="{ minRows: 4, maxRows: 5 }"
            placeholder="ËØ∑ËæìÂÖ•ÂÜÖÂÆπ"
            v-model="input_comment"
            maxlength="50"
            show-word-limit
          >
          </el-input>
          <el-button
            style="width: 150px; margin-top: 20px"
            icon="el-icon-s-comment"
