<script>
import Header from "../Header.vue";
import Footer from "../Footer.vue";
export default {
  components: { Header, Footer },
  async created() {
    const { data: res } = await this.$http.get(
      "/book/" + this.$route.params.book_id
    );
    this.book = res.data;
    // console.log(this.book);
    // ‰ªéÂêéÁ´ØapiÂàùÂßãÂåñbooks data
    const { data: res2 } = await this.$http.get("/book" + `?limit=10`);
    this.recommendBooks = res2.data;
    // console.log(this.recommendBooks);
    // ‰ªéÂêéÁ´ØapiÂàùÂßãÂåñcomment data
    const { data: res3 } = await this.$http.get(
      "/comment/" + this.book.book_id
    );
    this.comments = res3.data;
    console.log(this.comments);
  },
  data() {
    return {
      // postÈ°µÂõæ‰π¶
      book: {
        book_addr: "",
        book_author: "",
        book_date: "",
        book_dl_count: "",
        book_id: "",
        book_img_url: "",
        book_info: "",
        book_name: "",
        book_score: "",
        class_name: "",
      },
      // Êé®ËçêÂõæ‰π¶ÈõÜ
      recommendBooks: [],
      // ËØÑËÆ∫ÈõÜÂêà
      comments: [],
      // ËæìÂÖ•ËØÑËÆ∫
      input_comment: "",
    };
  },
  computed: {
    // ËÆ°ÁÆóËØÑÂàÜ Êï∞ÊçÆÂ∫ìÊï∞ÊçÆËåÉÂõ¥ÊòØ10ÔºåËÄåÁªÑ‰ª∂ËåÉÂõ¥ÊòØ5
    book_rate: {
      get() {
        return this.book.book_score * 0.5;
      },
      set(newValue) {},
    },
  },
  methods: {
    // ‰∏ãËΩΩÂõæ‰π¶
    downloadBook() {
      // Ê£ÄÊü•Áî®Êà∑ÊòØÂê¶ÁôªÈôÜ
      if (!this.$store.state.isLogin) {
        this.$router.push("/login");
        return this.$message.error("ËØ∑ÂÖàÁôªÈôÜ");
      }
      alert("Âè™ÊúâÂõæÁâáÊçèÔºÅ");
    },
    // Êî∂ËóèÂõæ‰π¶
    async collectBook() {
      if (!this.$store.state.isLogin) {
        this.$router.push("/login");
        return this.$message.error("ËØ∑ÂÖàÁôªÈôÜ");
      }
      const { data: res } = await this.$http({
        method: "post",
        url: "/collection?access_token=" + this.$store.getters.getToken,
        data: {
          user_id: this.$store.getters.getUserId,
          book_id: this.book.book_id,
        },
      });
      if (res.code == 200) {
        this.$message.success("Êî∂ËóèÊàêÂäüÔºÅ");
      } else {
        alert(res.data);
      }
    },
    // Ëé∑ÂèñËØÑËÆ∫
    async getComments() {
      const { data: res3 } = await this.$http.get(
        "/comment/" + this.book.book_id
      );
      this.comments = res3.data;
      console.log(this.comments);
    },
    // ÂèëÂ∏ÉËØÑËÆ∫
    async sendComment() {
      // È™åËØÅÁôªÂΩï
      if (!this.$store.state.isLogin) {
        this.$router.push("/login");
        return this.$message.error("ËØ∑ÂÖàÁôªÈôÜ");
      }
      // ËØÑËÆ∫ËØ≠Âè•‰∏ç‰∏∫Á©∫
      if (this.input_comment === "") {
        alert("ËØÑËÆ∫‰∏çËÉΩ‰∏∫Á©∫");
        return;
      }
      // ÂèëÈÄÅËØ∑Ê±Ç
      const { data: res } = await this.$http({
        method: "post",
        url: "/comment?access_token=" + this.$store.getters.getToken,
        data: {
          user_id: this.$store.getters.getUserId,
          book_id: this.book.book_id,
          comment_text: this.input_comment,
        },
      });
      // console.log(res);
      if (res.code === 200) {
        this.input_comment = "";
        this.getComments();
        this.$message.success("ËØÑËÆ∫ÂèëË°®ÊàêÂäüÔºÅ");
      } else {
        this.$message.error(res.data);
      }
    },
    // Âà†Èô§Ëá™Â∑±ÁöÑËØÑËÆ∫
    async delComment(comment_id) {
      // ÂèëÈÄÅËØ∑Ê±Ç
      const { data: res } = await this.$http({
        method: "delete",
        url:
          "/comment/" +
          comment_id +
          "?access_token=" +
          this.$store.getters.getToken,
      });
      if (res.code === 200) {
        this.getComments();
        this.$message.success("Âà†Èô§ÊàêÂäüÔºÅ");
      } else {
        this.$message.error(res.data);
      }
    },
  },
};
</script>

