    <Header></Header>
    <!-- ‰π¶Êú¨ÊòæÁ§∫‰∏ª‰Ωì -->
    <el-container class="main-container">
      <!-- logo -->
      <el-header class="logo-container" height="100px">
        <a href="javascript:;"><img src="../../public/logo.png" alt="" /> </a>
      </el-header>
      <!-- ÊêúÁ¥¢Ê°Ü -->
      <el-autocomplete
        placeholder="ÊêúÁ¥¢‰π¶Êú¨Á±ªÂûãÔºåÂêçÁß∞ÔºåÂá∫ÁâàÁ§æÔºåÊèèËø∞"
        v-model="searchText"
        class="input-with-select"
        :fetch-suggestions="querySearch"
        :trigger-on-focus="false"
        @select="handleSelect"
      >
        <template slot-scope="{ item }">
          {{ item }}
        </template>
        <el-button
          type="primer"
          slot="append"
          icon="el-icon-search"
          @click="searchBookItem"
        ></el-button>
      </el-autocomplete>
      <!-- ÊòæÁ§∫‰π¶Êú¨ÈÉ®ÂàÜ -->
      <el-main>
        <div class="divider">
          <h1>&nbsp;&nbsp;&nbsp;ÊúÄÂèóÊ¨¢ËøéÁöÑ</h1>
          <el-divider></el-divider>
        </div>
        <!-- ‰ø°ÊÅØÈÉ®ÂàÜ -->
        <div style="text-align: center">
          <div class="book_item" v-for="b in books">
            <a :href="`/book/` + b.book_id">
              <img
                :src="
                  `http://122.9.215.185:8081/static/img/books/` +
                  b.book_img_url.split('/').at(-1)
                "
                alt="‰π¶Êú¨ÂõæÁâá"
              />
            </a>
          </div>
        </div>
      </el-main>
    </el-container>
    <Footer></Footer>
  </div>
