<script>
import Footer from "./Footer.vue";
import Header from "./Header.vue";
export default {
  components: { Footer, Header },
  data() {
    return {
      // Êü•ËØ¢Âª∫ËÆÆ
      suggestionArray: [],
      searchText: "",
      // Êü•ËØ¢ÊéíË°åÊ¶úË°®ÂçïÊï∞ÊçÆ
      books: [],
    };
  },
  async created() {
    // ‰ªéÂêéÁ´ØapiÂàùÂßãÂåñbooks data
    const { data: res } = await this.$http.get("/book");
    this.books = res.data; 
  },
  methods: {
    // ÈÄâÊã©ÊêúÁ¥¢Âª∫ËÆÆ
    handleSelect(item) {
      if (this.select == "") {
        this.select = "book_name";
      }
      this.searchText = item;
    },
    // Ëé∑ÂèñÊêúÁ¥¢Âª∫ËÆÆ
    async querySearch(queryString, cb) {
      // console.log(str)
      // ÂèëÈÄÅÂêéÂè∞ËØ∑Ê±ÇËé∑ÂèñÊêúÁ¥¢Âª∫ËÆÆ
      const { data: res } = await this.$http({
        method: "get",
        url:
          "/book/suggestion?queryKey=" +
          (this.select != "" ? this.select : "book_name") +
          "&queryVal=" +
          queryString,
      });
      console.log(res.data);
      // Ë∞ÉÁî®callbackËøîÂõûÂª∫ËÆÆÁöÑÊï∞ÊçÆ
      cb(res.data);
    },
    // ÂØªÊâæ‰π¶Êú¨
    async searchBookItem() {
      if (this.select === "") {
        return this.$message.error("ËØ∑ÈÄâÊã©ÊêúÁ¥¢Á±ªÂà´");
      } else if (this.searchText === "") {
        return this.$message.error("ËØ∑ËæìÂÖ•ÊêúÁ¥¢ÂÜÖÂÆπ");
      }
      const { data: res } = await this.$http({
        method: "get",
        url: "/book" + "?" + this.select + "=" + this.searchText,
      });
      if (res.code === 200) {
        this.tableData = res.data;
        this.$message.success("ÊêúÁ¥¢ÊàêÂäü");
      } else {
        this.$message.error("ÊêúÁ¥¢Â§±Ë¥•");
      }
    },
  },
};
</script>

