import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

// ÂÆö‰πâ‰ºöËØùÁä∂ÊÄÅ
// ‰æø‰∫éÂà∑Êñ∞È°µÈù¢ÂêéÈáçÊñ∞‰ªélocalStorage‰∏≠Ëé∑ÂèñÂÄº
const state = {
  isLogin: localStorage.getItem("isLogin")
    ? 1
    : 0,
  // user
  userinfo: localStorage.getItem("userinfo")
    ? JSON.parse(localStorage.getItem("userinfo"))
    : null,
  // token
  token: localStorage.getItem("token") ? localStorage.getItem("token") : "", // token
  // ËèúÂçïÂØºËà™
  menu_data: [],
  //  Âä†ËΩΩÂä®ÊÄÅË∑ØÁî±Ê†áÂøó
  isLoadRoute: false, 
};

// ÂÆö‰πâË°å‰∏∫ÊñπÊ≥ï
const mutations = {
  //  ËÆæÁΩÆËèúÂçïÂØºËà™
  setMeunData(state, data) {
    state.menu_data = data;
  },
  //   ËÆæÁΩÆÂä†ËΩΩË∑ØÁî±Ê†áÂøó
  setLoadRoute(state, data) {
    state.isLoadRoute = data;
  },
  setToken(state, value) {
    // ËÆæÁΩÆÂ≠òÂÇ®token
    state.token = value;
    localStorage.setItem("token", value);
    localStorage.setItem("isLogin", 1);
    state.isLogin = 1;
  },
  setUserInfo(state, value) {
    // ËÆæÁΩÆÂ≠òÂÇ®token
    state.userinfo = JSON.parse(value);
    localStorage.setItem("userinfo", value);
  },
  removeStorage(state) {
    // Âà†Èô§token
    localStorage.removeItem("token");
    localStorage.removeItem("isLogin");
    localStorage.removeItem("userinfo");
    state.isLogin = 0;
  },
};

// ÂÆö‰πâËé∑ÂèñÂä†Â∑•ÊñπÊ≥ï
const getters = {
  // ÁõëÂê¨Êï∞ÊçÆÂèòÂåñÁöÑ
  getToken(state) {
    // Ëé∑ÂèñÊú¨Âú∞Â≠òÂÇ®ÁöÑÁôªÂΩï‰ø°ÊÅØ
    if (!state.token) {
      state.token = localStorage.getItem("token");
    }
    return state.token;
  },
  getUserId(state) {
    // Ëé∑ÂèñÁî®Êà∑id
    if (!state.userinfo) {
      state.userinfo = JSON.parse(localStorage.getItem("userinfo"));
    }
    return state.userinfo.user_id;
  },
  getUserLevel(state) {
    // Ëé∑ÂèñÁî®Êà∑ÊùÉÈôê
    if (!state.userinfo) {
      state.userinfo = JSON.parse(localStorage.getItem("userinfo"));
    } 
    return 1;//state.userinfo ? state.userinfo.user_level : 0;
  },
};

export default new Vuex.Store({
  state,
  mutations,
  getters,
});
