<template>
  <div class="login_container">
    <!-- ÁôªÂΩïÁõíÂ≠ê -->
    <div class="login_box">
      <img class="logo_img" src="../../../public/logo.png" alt="" />
      <!-- ÁôªÂΩïË°®ÂçïÂå∫Âüü -->
      <el-form
        ref="loginFormRef"
        :model="loginForm"
        :rules="loginFormRules"
        class="login_form"
      >
        <!-- accout -->
        <el-form-item prop="userId">
          <el-input
            v-model="loginForm.userId"
            prefix-icon="el-icon-user-solid"
            placeholder="user accout"
          >
          </el-input>
        </el-form-item>
        <!-- password -->
        <el-form-item prop="password">
          <el-input
            type="password"
            v-model="loginForm.password"
            prefix-icon="el-icon-lock"
            placeholder="password"
          >
          </el-input>
        </el-form-item>
        <!-- ÊåâÈíÆ -->
        <el-form-item class="btns">
          <a class="a_register" href="/regist">Ê≤°ÊúâË¥¶Âè∑ÔºüÁÇπÂáªÊ≥®ÂÜå</a>
          <el-button type="primary" @click="login">ÁôªÂΩï</el-button>
          <el-button type="info" @click="resetLoginForm">ÈáçÁΩÆ</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //ÁôªÂΩïË°®ÂçïÁöÑÊï∞ÊçÆÁªëÂÆöÂØπË±°
      loginForm: {
        userId: "",
        password: "",
      },
      // Ë°®ÂçïÁöÑÈ™åËØÅËßÑÂàôÂØπË±°
      loginFormRules: {
        //È™åËØÅÁî®Êà∑ÂêçÊòØÂê¶ÂêàÊ≥ï
        userId: [
          { required: true, message: "ËØ∑ËæìÂÖ•ÁôªÂΩïË¥¶Âè∑Âêç", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: "ÈïøÂ∫¶Âú® 3 Âà∞ 10 ‰∏™Â≠óÁ¨¶",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "ËØ∑ËæìÂÖ•ÁôªÂΩïÂØÜÁ†Å", trigger: "blur" },
          {
            min: 6,
            max: 15,
            message: "ÈïøÂ∫¶Âú® 6 Âà∞ 15 ‰∏™Â≠óÁ¨¶",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    // ÁôªÂΩï
    login() {
      this.$refs.loginFormRef.validate(async (valid) => {
        if (!valid) return;
        // ÂèëÈÄÅÁôªÂΩï
        var targetURL =
          "?userid=" +
          this.loginForm.userId +
          "&password=" +
          this.loginForm.password;
        const { data: res } = await this.$http({
          method: "get",
          url: "/user" + targetURL,
        });
        if (res.code != 200)
          return this.$message.error("ÁôªÂΩïÂ§±Ë¥•! " + res.data);
        //Â∞ÜÁôªÂΩïÊàêÂäü‰πãÂêéÁöÑtokenÔºå‰øùÂ≠òÂà∞ÂÆ¢Êà∑Á´ØÁöÑsessionStorage‰∏≠
        this.$store.commit("setToken", res.data.access_token);
        this.$message.success({
          message: "ÁôªÂΩïÊàêÂäü",
        });
        // ÊãâÂèñÁî®Êà∑‰ø°ÊÅØ
        const { data: res2 } = await this.$http.get(
          "/user/" +
            this.loginForm.userId +
            "?access_token=" +
            res.data.access_token
        );
        if (res2.code != 200)
          return this.$message.error("ÊãâÂèñÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥•ÔºÅ" + res2.data);
        // Â∞ÜÁî®Êà∑‰ø°ÊÅØ‰øùÂ≠òÂà∞store,localStorage‰∏≠
        this.$store.commit("setUserInfo", JSON.stringify(res2.data));
        // Ë∑≥ËΩ¨Âà∞ÂêéÂè∞‰∏ªÈ°µ
        this.$router.push("/home");
      });
    },
    // ÈáçÁΩÆÁôªÂΩïË°®Âçï
    resetLoginForm() {
      this.$refs.loginFormRef.resetFields();
    },
  },
};
</script>

<style lang="less" scoped>
// ÁôªÂΩïbodyÂÆπÂô®
.login_container {
  height: 100%;
  background-image: url("../../../public/bg.jpg");
  background-size: 100% 100%;
}

// ÁôªÂΩïÁõíÂ≠ê
.login_box {
  text-align: center;
  width: 500px;
  height: 400px;
  padding-top: 15px;
  background-color: rgba(255, 255, 255, 0.95);
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  img {
    height: 100px;
  }
}

// ÁôªÂΩïË°®ÂçïÂå∫Âüü
.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 28px 20px;
  box-sizing: border-box;
}

// ÊåâÈíÆÂå∫Âüü
.btns {
  display: flex;
  justify-content: flex-start;
}

// Ê≥®ÂÜåË∂ÖÈìæÊé•
.a_register {
  margin: auto;
  text-decoration: none;
  color: blue;
  margin-right: 150px;
}

// ÁßªÂä®Á´ØÈÄÇÈÖç
@media (max-width: 600px) {
  .login_box {
    width: 95%;
  }
  .login_form {
    bottom: 30px;
  }
  .a_register {
    margin-right: 20px;
    font-size: 16px;
  }
}
</style>
